.carousel{
    background-color: blue;
    padding-left: 400px;
    padding-top: 100px;
    align-items: center;
}
p,h3{
  font-weight: bold;
  width:200px;
  font-family: 'Courier New', Courier, monospace;
}
@media screen and (max-width:600px){
  .slide{
    position: relative;
    right:10px;
   
  }
  .pic{
    width:600px;
    height: 1000px;
    object-fit: cover;
    z-index: 1;
  }
  .Carousel{
   
    position: relative;
    right:0px;
    padding:100px;
  }
  h3{
    position: relative;
    right:500px;
  }
}

