.testimonials{
    background-color:  blue;
    display: flex;
    justify-content: space-evenly;
    
}
@media screen and (max-width:600px){
    .testimonials{
        flex-direction: column;
        margin-left: 200px;}}

h3{
    margin-left: 50px;
    padding: 10px;
    width: 500px;
    margin-top: 40px;
    font-size: 1.4rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}

.test{
    margin-top: 30px;
    padding:10px;
    border:none;
    border-radius: 5px;
    width:200px;
    height:300px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    object-fit: contain;
    margin-bottom: 30px;
}

.img2{
    border-radius: 100%;
    height: 100px;
    width: 100px;
    object-fit: contain;
    border:1px solid grey;
    margin-left: 40px;
    margin-bottom: 20px;
}
.comment{
    width:120px;
    margin-left: 40px;
    color:grey;
}

.stars{
    margin-left: 35px;
}