.hamburger{
    width:50px;
    cursor: pointer;

 
}
.line{
    margin-bottom: 5px;
    margin-left: 300px;
    width: 25px;
    height: 2px;
   background-color: grey;
    border:1px solid black;
    cursor: pointer;
}

@media screen and (max-width:600px){
    .line{
        visibility: visible;    
    }
    .hamburger{
        position: relative;
        right:100px;
        border:1px solid white;
    }
}
@media screen and (min-width:600px){
    .line{
       display: none;   
    }}
