.header{
    background-color: darkblue;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 0px;
    position: fixed;
    z-index: 1;
    width: 100%;

}



h1{color:white;
    font-family: 'Courier New', Courier, monospace;
text-decoration:dotted;
text-underline-offset: none;
display: flex;
align-items: center;
font-weight: bold;
padding-left:15px;
padding-top:15px;}

Link{
    text-decoration: none;
}

ul{ padding-top:15px;
    display: flex;
    justify-content: space-between;
}

li{
    
    font-weight: bold;
    list-style: none;
    padding: 10px;
    text-decoration: none;
}
@media screen and (max-width: 600px) {
    li {
      visibility: hidden;
    }
  }
li:hover{
    color:white;
}